.contestform-container {
	width: 50%;
	height: 80vh;
	overflow-y: scroll;
	overflow-x: hidden;
}

.contestForm {
	width: 35vw;
}

.contestForm h3 {
	color: var(--text-dim);
}

.contestForm .MuiTextField-root {
	width: 45%;
}

.contestForm .MuiOutlinedInput-root,
.MuiInputLabel-root {
	color: var(--text-dim) !important;
}

.contestForm .MuiInputLabel-root {
	color: var(--text-dim) !important;
	opacity: 0.4;
}

.contestForm .MuiSelect-root {
	width: 45%;
	color: inherit;
}

.contestForm .MuiSelect-icon {
	color: var(--text-dim);
}

.MuiCalendarPicker-root {
	background-color: var(--background-2) !important;
}

.MuiCalendarPicker-root .MuiPickersDay-root,
.MuiPickersDay-root {
	background-color: var(--background-2);
}

.MuiCalendarPicker-root .MuiTypography-root {
	color: var(--text-dim);
}

.MuiClockPicker-root {
	background-color: var(--background-2) !important;
}

.css-1flhz3h {
	color: var(--text-dim) !important;
}
