.addPrice .Mui-disabled .MuiOutlinedInput-input {
	-webkit-text-fill-color: var(--text-dim);
	opacity: 0.8;
}

.contestform-toggle .MuiSwitch-track {
	background-color: rgba(75, 75, 75, 0.6) !important;
	opacity: 1;
}

.w-45 {
	width: 45%;
}

.MuiFormControlLabel-label.Mui-disabled {
	color: var(--text-dim) !important;
}

.MuiFormControlLabel-root.Mui-disabled {
	opacity: 0.2;
}

.MuiSwitch-switchBase {
	color: var(--primary) !important;
}

.MuiButtonBase-root-MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track {
	opacity: 1;
}
.Mui-selected {
	background-color: rgba(255, 255, 255, 0.2) !important;
}

.MuiPickersDay-root:not(.Mui-selected) {
	border: 1px var(--primary);
}

.css-1v994a0 {
	color: var(--text-dim);
}

.random-toggle .MuiFormControlLabel-root {
	margin-left: 0;
}

.duplicateIcon .MuiAlert-root {
	width: 90%;
	margin: 2rem 0;
}

.duplicateIcon .MuiAlert-icon svg {
	color: #03a9f4 !important;
}
