.contest-manager {
	width: 100%;
	overflow-y: scroll;
	height: 100vh;
}

.contests-row {
	width: 100%;
}

.contest-thumbnail {
	width: 10%;
}

.contest-thumbnail img {
	width: 50px;
	height: 50px;
	object-fit: cover;
	border-radius: 5px;
}

.contest-title {
	width: 25%;
}

.contest-date {
	width: 15%;
}

.contest-type {
	width: 10%;
}

.contest-host {
	width: 15%;
}

.contest-status {
	width: 15%;
}

.contest-total {
	width: 5%;
}

.contest-status .MuiOutlinedInput-root {
	width: 8rem;
	color: var(--text-dim);
	height: 2.5rem;
}
.contest-status .MuiOutlinedInput-root svg {
	color: var(--text-dim);
}

.contest-status
	.css-1yk1gt9-MuiInputBase-root-MuiOutlinedInput-root-MuiSelect-root
	svg {
	color: var(--text-dim);
}

.btn-disabled .Mui-disabled .MuiOutlinedInput-input {
	-webkit-text-fill-color: var(--text-dim) !important;
	color: var(--text-dim) !important;
	opacity: 0.9;
}

.contestTable-room {
	width: 30vw;
	background-color: var(--background-2);
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: var(--radius-small);
}
