.createGame-modal {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--surface);
	border-radius: var(--radius-small);
	border: 2px solid #ffffff52;
	min-width: 30vw;
}

.create-game input {
	width: 100%;
	transition: 0.3s ease-in-out;
}

.ruleForm .MuiSelect-root,
.ruleForm .MuiSelect-root svg {
	color: var(--text-dim) !important;
}

.ruleForm input,
.ruleForm .MuiSelect-root {
	width: 7rem;
}

.maps-input input {
	width: 100% !important;
}

.ruleForm textarea {
	max-width: 100%;
	max-height: 8em;
	resize: none;
}

.rules-desc-container {
	width: 100%;
}

.rules-desc {
	border: 1px solid var(--text-dim);
	border-radius: 4px;
	width: 90%;
	padding: 0.25rem 0.5rem;
}

.rules-desc-button {
	width: 10%;
}

.rules-descrptions-container,
.preview-container {
	overflow-y: scroll;
	max-height: 20rem;
}

.slot-previews-container {
	width: 31vw;
	overflow-y: scroll;
	height: 15rem;
}

/* .slot-previews {
} */

.preview-panel {
	width: 34vw;
	margin-left: 2rem;
	border-left: 2px dashed #ffffff85;
}

.dashed-border-left {
}

.rules-container {
	width: 60vw;
}

/* .rules-preview-container {

} */

.timeslot-inputs .MuiInputLabel-root {
	color: var(--text-dim) !important;
}

.timeslot-inputs .MuiInputLabel-root,
.timeslot-inputs .Mui-focused {
	color: var(--text-dim) !important;
	opacity: 0.4;
}

.slot-preview {
	position: relative;
	width: 6rem;
	height: 6rem;
	background-color: rgba(255, 255, 255, 0.1);
	border-radius: var(--radius-small);
}

.slot-preview-delete {
	position: absolute !important;
	top: -10px;
	width: max-content;
	height: max-content;
	background-color: transparent;
	border-radius: 4px;
	border: 0;
	padding: 0;
	right: -10px;
	transition: 0.3s ease-in-out;
}

.slot-preview-delete :hover {
	cursor: pointer;
	background-color: rgba(255, 255, 255, 0.1);
}

.MuiSvgIcon-root {
	color: var(--text-dim) !important;
}

.coupon-inputs {
	width: 20vw;
}

.coupon-inputs .MuiTextField-root {
	margin-bottom: 1rem;
}

.coupon {
	width: 100%;
	/* border: 2px solid #ff6e31; */
	background-color: #76549a; /* prev df7861 */
	border-radius: 15px;
	position: relative;
}

.coupon-free {
	position: absolute;
	top: -0.5rem;
	right: -0.5rem;
	border-radius: 10px;
	background-color: #df4d19;
	font-weight: 600;
}
