.searchbar {
	width: 100%;
	position: relative;
}

.search {
	width: 85%;
	position: relative;
}

.searchbar .css-1u3bzj6-MuiFormControl-root-MuiTextField-root {
	width: 100% !important;
}

.search-select,
.search button {
	position: absolute !important;
	z-index: 1;
}

.search button {
	right: -0.75rem;
	top: 50%;
	width: 3rem;
	height: 3rem;
	transform: translateY(-50%);
	background-color: var(--primary-light);
	color: var(--surface) !important;
}

.search button:hover {
	background-color: var(--primary-light);
}

.search-input .MuiOutlinedInput {
	padding: 0.5rem;
}

.has-select .MuiOutlinedInput-root {
	padding-left: 11rem !important;
}

.searchbar .MuiOutlinedInput-root {
	border: 0 !important;
	outline: 0 !important;
	border-radius: 60px !important;
	background: var(--background);
	color: var(--text-dim);
	/* font-size: 13px; */
}

.searchbar .MuiInputBase-input {
	padding: 0.5rem 0.75rem;
}

.search-select svg {
	color: var(--text-dim);
}

.searchbar .MuiOutlinedInput-notchedOutline {
	border: 0 !important;
}

.search-button button {
	color: var(--primary-light) !important;
}

.search-select .MuiSelect-select {
	border: 0;
	background-color: var(--background-2);
	border-radius: 20px;
}

.search-select {
	width: 10rem;
	top: -0.15rem;
	left: -0.5rem;
	text-transform: capitalize;
}

.filterbox {
	position: absolute;
	right: 3rem;
	top: 4rem;
	z-index: 10;
	background-color: var(--background-light);
	border-radius: 10px;
	min-width: 15rem;
	border: 2px solid #ffffffaa;
}

.filterbox button {
	width: max-content;
	padding: 0.05rem 0 !important;
}
