.reportedList {
	width: 50vw;
	height: 60vh;
	border-radius: 10px;
	background-color: var(--background-2);
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

.reported-datas {
	height: 46vh;
	/* max-height: 90%; */
	overflow: hidden;
	overflow-y: scroll;
}

.reported-header {
	font-weight: 600;
}

.reported-row p {
	width: 33.33%;
}
