.contestDetails {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--surface);
	border-radius: var(--radius-small);
	width: 70vw;
}

.contestData {
	width: 50%;
}

.contestData-placeholder {
	width: 25rem;
	height: 100%;
	border-radius: 10px;
	object-fit: contain;
}

.contest-row {
	margin-bottom: 0.75rem;
}

.contest-row p {
	max-width: 50%;
}
