.chats-container {
	width: 100%;
	height: 100vh;
}

.chat-user-container {
	width: 25vw;
}

.chat-card {
	width: 100%;
	height: 8rem;
	background-color: var(--background-2);
	border-radius: var(--radius-extra-small) !important;
	border: 1px solid rgba(0, 0, 0, 0);
	transition: 0.3s ease-in-out;
}

.chat-card:hover {
	cursor: pointer;
	/* transform: scale(1.025); */
	background-color: rgba(255, 255, 255, 0.083) !important;
	border: 1px solid var(--primary-light);
}

.chat-avatar img {
	width: 50px;
	height: 50px;
	border-radius: 50%;
}

.chats-container .active {
	background-color: rgba(255, 255, 255, 0.83) !important;
}

.chat-box {
	width: 53vw;
}

.chat-box-header {
	background-color: rgba(255, 255, 255, 0.05);
}

.chatTextBox {
	padding: 0.5rem !important;
	border: 0 !important;
}

.chatTextBox {
	border: 0 !important;
	outline: 0 !important;
	/* border-radius: 60px !important; */
	background: var(--background);
	color: var(--text-dim);
	/* font-size: 13px; */
}
.chatTextBox {
	padding: 0.85rem;
}
.chatTextBox textarea {
	border: 0 !important;
	background-color: transparent !important;
	height: 8rem;
	resize: none;
}

.chat-box {
	height: 100%;
}

.chat-box-body {
	height: 70vh;
	background-color: rgba(255, 255, 255, 0.02);
	overflow-y: scroll;
}

.chat-message span {
	font-size: 12px;
	margin-top: 0.45rem;
	opacity: 0.4;
}

.chat-left .chat-message span {
	text-align: right;
}

.chat-right .chat-message {
	background-color: var(--primary-dark);
	width: 45%;
	border-radius: 8px 8px 0 8px;
}
.chat-left .chat-message {
	border-radius: 8px 8px 8px 0;
	width: 45%;
	background-color: rgba(255, 255, 255, 0.1);
}
