.gameList {
}

.games-card {
	width: 17rem;
	border-radius: var(--radius-extra-small) !important;
	background-color: var(--background-2) !important;
	overflow: hidden;
	transition: 0.25s ease-in-out !important;
	border: 2px solid var(--background-2) !important;
}

.games-card:hover {
	border: 2px solid var(--primary-light) !important;
	border-radius: var(--radius-small) !important;
}

.games-card img {
	width: 100%;
	height: 8rem;
	border-radius: var(--radius-extra-small) !important;
	object-fit: cover;
}

.games-card p {
	font-size: 1rem;
	font-weight: 500;
	text-overflow: ellipsis;
}

.game-settings {
	border-radius: 0 !important;
	border-bottom: 2px solid #f36b7277 !important;
}

.game-settings:hover {
	background-color: #ed1c2411 !important;
}

.game-contests {
	text-align: center;
	width: 100%;
	border-radius: 0 !important;
}
