@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

:root {
	--primary: #ed1c24;
	--primary-dark: rgba(243, 107, 113, 0.2);
	--primary-light: #f36b71;
	--secondary: #8aea92;
	--surface: #121212;
	--background: rgba(255, 255, 255, 0.04);
	--background-2: #1b1b1b;
	--background-light: #1b1b1b;
	--text-light: #ffffff;
	--text-dim: #ffffffde;
	--radius-extra-small: 4px;
	--radius-small: 0.75rem;
	--radius-large: 2rem;
	--gold: #a47e3b;
	--blue: #006aa3;
	--dark-green: #567661;
	--danger: #cb1531;
	--danger-dark: #820000;
	--dark-brown: #3c2a21;
	--light-cyan: #72b5b7;
	--charcoal: #30475e;
	--purple: #76549a;
}

* {
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Lato', sans-serif;
	color: var(--text-dim);
	background-color: var(--surface);
	width: 100vw;
	min-height: 100vh;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
}

button {
	width: max-content;
	border-radius: var(--radius-large) !important;
	color: var(--text-light) !important;
	font-family: 'Lato', sans-serif !important;
}

.contained {
	background-color: var(--primary) !important;
	transition: 250ms cubic-bezier(0.4, 0, 0.2, 1) !important;
	padding: 0.5rem 1rem !important;
	height: max-content;
}

.contained:hover {
	transform: scale(0.95);
}

.contained-light {
	background-color: var(--primary-light) !important;
	transition: 250ms cubic-bezier(0.4, 0, 0.2, 1) !important;
	padding: 0.5rem 1rem !important;
	font-size: 16px;
	color: var(--background-2) !important;
	height: max-content;
}

.outlined {
	border: 1px solid var(--primary-light) !important;
	color: var(--primary-light) !important;
}

.contained-outlined {
	color: var(--text-light) !important;
	border: 1px solid var(--primary-light) !important;
	background-color: #f36b728e !important;
	padding: 0.25rem 0.75rem !important;
}

.contained-background {
	background-color: #333333 !important;
}

.btn-disabled {
	opacity: 0.35 !important;
	cursor: not-allowed !important;
}

.accept-btn {
	color: #72bbff !important;
}

.reject-btn {
	color: var(--primary-light) !important;
}

.customButton {
	background-color: var(--surface);
	border: 0;
	font: inherit;
	font-size: 14px;
	border: 2px solid transparent;
	transition: 0.3s ease-in-out;
	border-radius: 4px !important;
}

.customButton:hover {
	cursor: pointer;
	background-color: var(--primary);
}

.skeleton {
	animation: skeleton-loading 1s linear infinite alternate;
}

@keyframes skeleton-loading {
	0% {
		background-color: hsl(200, 20%, 80%);
	}
	100% {
		background-color: hsl(200, 20%, 95%);
	}
}

.text-accent-light {
	color: var(--primary-light) !important;
}

.MuiOutlinedInput-root::placeholder {
	color: var(--text-dim) !important;
}

.MuiOutlinedInput-root {
	color: var(--text-dim) !important;
}

.MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
	border-color: var(--text-dim) !important;
}

.Mui-error input {
	border-color: #d32f2f !important;
}

a {
	color: var(--text-dim) !important;
	font-family: 'Lato', sans-serif !important;
	text-transform: none !important;
	border-radius: var(--radius-large) !important;
}

