.box-upload {
	width: 100%;
	height: 15rem;
	overflow: hidden;
	position: relative;
	border-radius: var(--radius-small);
	background-color: var(--background);
}

.box-upload-input input {
	width: 100%;
	height: 100%;
	position: absolute;
	cursor: pointer;
	opacity: 0;
	z-index: 1;
}

.box-upload-input span {
	position: absolute;
	bottom: 0.5rem;
}

.box-error {
	border: 1px solid #d32f2f;
}

.box-upload-preview {
	width: 100%;
	position: relative;
	height: 100%;
}

.box-upload-preview img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.box-upload-preview button {
	position: absolute;
	top: 0.5rem;
	right: 0.5rem;
	width: 1.4rem;
	min-width: 1rem;
	height: 1.4rem;
	background-color: var(--primary);
	padding: 0.5rem !important;
}

.box-upload-preview button:hover {
	background-color: var(--text-dim);
	color: var(--primary) !important;
}
