.dashboard {
	height: 100vh;
	overflow-y: scroll;
}

.d_title {
	font-family: Poppins;
	font-size: 48px;
	font-style: normal;
	font-weight: 700;
	line-height: normal;
	letter-spacing: -0.96px;	
	padding: 36px 0 0 36px;
}

.main_card_div{
	display: flex;
	justify-content: space-between;
	margin: 0px 10px;
}

.card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	width: 161px;
	height: 258px;
	padding: 29px 32px;
	border-radius: 5px;
	color: var(--landing-primary, #FFF);
	background: var(--background_lvl_2, linear-gradient(0deg, rgba(255, 255, 255, 0.09) 0%, rgba(255, 255, 255, 0.09) 100%), #121212);
	margin-left: 36px;
}

.card:hover{
	background: radial-gradient(79.87% 202.47% at 50% 50%, rgba(48, 180, 93, 0.70) 0%, rgba(48, 180, 93, 0.00) 100%);
}

.card_title {
	font-family: Lato;
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
}

.count_parent{
	position: relative;
}

.count_parent::after{
	content: '';
	width: 100%;
	height: 100%;
	border-radius: 5px;
	opacity: 0.5;
	filter: blur(28px);
	position: absolute;
}

.cp1::after{
	background: rgba(48, 180, 93, 0.70);
}

.cp2::after{
	background: radial-gradient(169.31% 169.3% at 50% 50%, rgba(114, 187, 255, 0.70) 0%, rgba(114, 187, 255, 0.00) 100%)
}

.cp3::after{
	background: radial-gradient(107.03% 107.02% at 50% 50%, rgba(253, 216, 53, 0.70) 0%, rgba(253, 216, 53, 0.00) 100%);
}

.user_count{
	font-family: Poppins;
	font-size: 34px;
	font-style: normal;
	font-weight: 700;
	padding-left: 8px;border-radius: 5px;
}

.r_title{
	font-family: Poppins;
	font-size: 34px;
	font-style: normal;
	font-weight: 700;
	line-height: normal;
	padding: 36px 0 0 36px;
}

.refferal_card{
	height: 449px;
	width: 486px;
	border-radius: 5px;
	background: var(--background_lvl_2, linear-gradient(0deg, rgba(255, 255, 255, 0.09) 0%, rgba(255, 255, 255, 0.09) 100%), #121212);
	padding: 36px 32px 29px 32px;
	margin-left: 36px;
}

.table-con{
	overflow-y: scroll;
    width: 100%;
	height: 360px;
}