.notification {
	width: 100%;
	overflow-y: scroll;
	height: 100vh;
}

.sentNotification {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--surface);
	width: 40vw;
	border-radius: var(--radius-small);
	border: 2px solid #ffffff52;
}

.sentNotification .header {
	width: 100%;
	padding: 1rem 0.5rem;
	border-radius: 6px;
	background-color: var(--primary-dark);
}

.sentNotification textarea {
	height: 16vh;
	resize: none;
}

.detailsModal {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--surface);
	width: 31vw;
	border-radius: var(--radius-small);
	border: 2px solid #ffffff52;
}

.detailModalRow {
	display: flex;
	justify-content: space-between;
	margin-bottom: 0.5rem;
}

.detailModalRow p:last-child {
	/* text-align: end; */
	margin-left: 1rem;
	max-width: 18vw;
}

.detailsModal-body {
	max-height: 70vh;
	overflow: scroll;
}
