.dialog-box {
	width: 25vw;
	background-color: var(--background-2);
	border-radius: var(--radius-small);
	position: absolute;
	left: 50%;
	top: 50%;
	max-height: 50vh;
	transform: translate(-50%, -50%);
    overflow: hidden;
}

.dialog-box-heading {
	background-color: rgba(243, 107, 113, 0.2);
	border-radius: 6px;
}
