.rdrDefinedRangesWrapper, .rdrCalendarWrapper, .rdrDateDisplayWrapper, .rdrDateDisplayItem{
    background: #272727;
}

.rdrDefinedRangesWrapper{
    border-radius: 10px 0 0 10px;
}

.rdrCalendarWrapper, .rdrDateDisplayWrapper{
    border-radius: 0 10px 10px 0;
}

.rdrStaticRanges button{
    background: #272727;
    border: none;
    width: auto;
}

.rdrStaticRangeLabel:hover{
    color: #272727;
}

.rdrDefinedRangesWrapper{
    border-right: none;
}

.rdrDays{
    background-color: #727272;
    color: #fff !important;
}