.table-container {
	height: 72vh !important;
	overflow: scroll;
	width: 100%;
}

.table {
	border-collapse: separate;
	border-spacing: 0 10px;
	position: relative;
	width: inherit;
}

.table-col {
	font-weight: 500;
	text-align: start;
	padding: 0.5rem;
}

thead {
	position: sticky;
	top: 0;
	background-color: var(--surface);
	z-index: 3;
}

.table-row {
	font-size: 13px;
	background-color: var(--background);
}

.table-row button {
	font-size: 13px;
	text-align: start;
}

td:first-child {
	/* border-left-style: solid; */
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}

td:last-child {
	/* border-right-style: solid; */
	border-bottom-right-radius: 10px;
	border-top-right-radius: 10px;
}

.table-row td {
	padding: 0.25rem 0.5rem;
}

tbody tr {
	/* outline: 2px solid var(--secondary) !important; */
}

tbody tr:hover {
	outline: 2px solid #86bbd8 !important;
}

.gotoContainer {
	border: 1px solid #ffffff3e;
	width: 10vw;
	position: relative;
	border-radius: 4px;
}

.gotoContainer input {
	background-color: var(--background);
	outline: none;
	padding: 0.25rem 0.75rem;
	color: var(--text-light);
	width: 100%;
	border: 0;
}

.gotoContainer button {
	position: absolute;
	right: 0;
}
