.gameManager {
	width: 100%;
	overflow-y: scroll;
	height: 100vh;
}
.game-details img {
	height: 20vw;
	width: 100%;
	object-fit: contain;
}

.game-detail {
	width: 40%;
}

.showRules {
	width: 45%;
}

.editRules {
	width: 45%;
}

.editRules-editor {
	width: 80%;
}

.editRules-editor textarea {
	max-width: 100%;
	max-height: 50rem;
	resize: none;
}

.gameSlot-container {
	width: 45%;
}

.gameSlot-content {
	width: 50%;
}

.gameSlot-previews {
	max-height: 45vh;
	overflow-x: hidden;
	overflow-y: scroll;
}

.gameSlot-preview {
	position: relative;
	width: 11rem;
	height: 6rem;
	background-color: rgba(255, 255, 255, 0.1);
	border-radius: var(--radius-small);
}

.timeslot-form {
	width: 90%;
}

.ruleForm .MuiSelect-root,
.ruleForm .MuiSelect-root svg {
	color: var(--text-dim) !important;
}

.ruleForm input,
.ruleForm .MuiSelect-root {
	width: 7rem;
}

.ruleForm textarea {
	max-width: 100%;
	max-height: 8em;
	resize: none;
}

.rules-desc-container {
	width: 100%;
}

.rules-desc {
	border: 1px solid var(--text-dim);
	border-radius: 4px;
	width: 90%;
	padding: 0.25rem 0.5rem;
}

.rules-desc-button {
	width: 10%;
}

.rules-descrptions-container {
	overflow-y: scroll;
	max-height: 20rem;
}

.slot-previews-container {
	width: 31vw;
	overflow-y: scroll;
	height: 15rem;
}

.rules-preview {
	width: 34vw;
	margin-left: 2rem;
	border-left: 2px dashed #ffffff85;
}

.rules-container {
	width: 60vw;
}

.create-game {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: var(--surface);
	border-radius: var(--radius-small);
	min-width: 30vw;
}

.gameManager-gameDetails {
	font-size: 14px;
	opacity: 0.6;
}

.gameManager-gameDetails svg {
	font-size: 7px;
}
