.userProfile {
	width: 100%;
	overflow-y: scroll;
	height: 100vh;
}

/* **** user data **** */

.user-data {
	width: 100%;
}

.profile {
	width: 35%;
	text-align: center;
}

.profile button {
	color: var(--primary-light) !important;
}

.profile img {
	width: 150px;
	height: 150px;
	object-fit: cover;
	border-radius: 50%;
}

.main-details {
	background-color: var(--background);
	border-radius: 5px;
}

.detailBox-container {
	width: 100%;
}

.details-box {
	width: 16rem;
	max-width: 16rem;
}

.details-box p:nth-child(1) {
	opacity: 0.5;
	margin-bottom: 0.5rem;
}

.wallet-details div {
	width: 170px;
	height: 135px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border-radius: 5px;
	color: var(--surface);
	position: relative;
	overflow: hidden;
	transition: 0.25s ease-in-out;
	border: 2px solid #00000000;
}

.wallet-details div:hover {
	border-radius: 10px;
	border: 2px solid var(--primary);
	box-shadow: 0px 0px 15px 1px var(--surface) inset;
}

.wallet-details p {
	z-index: 3;
}

.wallet-1 {
	background-color: #72bbff;
}

.wallet-2 {
	background-color: #ffcd86;
}

.wallet-3 {
	background-color: #f36b71;
}

.wallet-4 {
	background-color: #f6f6f6;
}

.wallet-details div img {
	position: absolute;
}

.ball {
	border-radius: 50%;
	position: absolute;
}

.ball-1 {
	width: 180px;
	height: 180px;
	left: -5.8rem;
	top: 0rem;
}

.ball-2 {
	width: 130px;
	height: 130px;
	right: -1.5rem;
	top: 5rem;
}

.ball-a1 {
	background-color: #67a8e6;
}
.ball-a2 {
	background-color: #5b96cc;
}

.ball-b1 {
	background-color: #e6b979;
}
.ball-b2 {
	background-color: #cca46b;
}

.ball-c1 {
	background-color: #db6066;
}
.ball-c2 {
	background-color: #c2565a;
}

.ball-d1 {
	background-color: #dddddd;
}
.ball-d2 {
	background-color: #c5c5c5;
}

.wallet-details div img:nth-child(3) {
	bottom: -4.5rem;
	right: -1rem;
	opacity: 0.3;
	width: 130px;
}

.wallet-details div img:nth-child(4) {
	top: -0.1rem;
	left: -5.8rem;
	width: 200px;
	height: 200px;
	opacity: 0.2;
}

.wallet-details div p:nth-child(1) {
	font-weight: 600;
	font-size: 1rem;
}

.wallet-details div p:nth-child(2) {
	font-weight: 600;
	font-size: 1.2rem;
	letter-spacing: 0.02em;
	opacity: 0.7;
	font-family: 'Poppins', sans-serif;
}

/* **** User Game Data **** */
.user-gameData h3 {
	color: var(--text-dim);
	letter-spacing: 0.11rem;
}

.userprofile-row {
	width: 100%;
}

.userprofile-row td {
	padding: 0.5rem 1rem !important;
}

.tm-Buttons button {
	border-radius: 4px 4px 0 0 !important;
	padding: 1rem;
	text-transform: none !important;
}

.tm-Buttons .active {
	background-color: var(--background-2);
}

.tm-container {
	background-color: var(--background-2);
}

.gametype .active {
	background-color: rgba(243, 107, 113, 0.1);
}

.tm-container thead {
	background-color: var(--background-2);
}

/* deposit history row  */

.depositHistory-row {
	padding: 0.5rem 1rem;
}

.date {
	width: 12%;
}

.amount {
	width: 8%;
}
.status {
	width: 8%;
}
.amount_paid {
	width: 8%;
}
.amount_due {
	width: 10%;
}
.currency {
	width: 10%;
}
.order_id {
	width: 15%;
}
.reciept {
	width: 15%;
}

.status_trans {
	width: max-content;
	padding: 0.15rem 0.5rem;
	border-radius: 4px;
}
.created {
	background-color: var(--gold);
}

.processing,
.authorized {
	background-color: var(--blue);
}

.processed,
.captured {
	background-color: var(--dark-green);
}

.reversed,
.failed {
	background-color: var(--danger);
}

.rejected {
	background-color: var(--danger-dark);
}

.refunded {
	background-color: var(--dark-brown);
}

.attempted {
	background-color: var(--charcoal);
}
